
/* Tab Layout -- Start */

.tab_container {
  width: 600px;
  position: relative;
}


.stream_tab {
  padding-top: 30px;
  height: 100vh;
  position: relative;
  position: fixed;
  width: 600px;
  border-left: 1px solid rgb(236, 236, 236);
  background-color: rgb(247, 247, 247); 
}

.stream_tab__scrollable {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding-bottom: 150px;
}

.stream_tab__container {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

@media (max-width: 1000px) {
  .tab_container {
    width: 400px;
  }

  .stream_tab {
    width: 400px;
  }
}

@media (max-width: 768px) {
  .tab_container {
    width: 300px;    
  }

  .stream_tab {
    width: 300px;    
  }
}

@media (max-width: 600px) {
  .tab_container {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 2;
  }

  .stream_tab {
    width: 100%;
  }

  .stream_tab__container {
    display: block;
    word-wrap: break-word;
  }

  .stream_tab__full_icon {
    display: none;
  }
}

.stream_tab--full {
  width: 100%;
  top: 0;
  right: 0;
  z-index: 2;  
}

/* Tab Layout -- End */


/* Tab Content -- Start */

.stream_tab__header {
  font-weight: 200;
  padding: 10px;
  margin: 0 auto;
  margin-top: 15px;
}

.stream_tab__select {
  margin-top: 30px;
}

.stream_tab__instance-box {
  position: relative;
  -ms-flex: 1 1 250px;
      flex: 1 1 250px;  
}

.stream_tab__instance {
  padding: 10px;
  margin: 10px;
  height: 200px;
  overflow-y: hidden;
  border: 1px solid lightgray;
}

.instance--hasdata:hover {
  border-color: rgb(180, 250, 180);
  cursor: pointer;
}

.instance--nodata:hover {
  border-color: rgb(236, 177, 177);  
}

.instance__show {
  color: rgb(44, 44, 44);
  padding: 0 5px;
  position: absolute;
  background-color: #cccccc;
  opacity: 0.8;
  bottom: 10px;
  left: 25px;
  border-radius: 5px;
  -webkit-box-shadow: 1px 3px 5px rgb(109, 109, 109);
          box-shadow: 1px 3px 5px rgb(109, 109, 109);
  -webkit-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

.instance__show:hover {
  opacity: 0.9;  
  bottom: 15px;  
  -webkit-box-shadow: 3px 5px 10px rgb(109, 109, 109);  
          box-shadow: 3px 5px 10px rgb(109, 109, 109);
}

.instance__text {
  color: rgb(163, 163, 163);
  font-style: italic; 
}

.stream_tab__toggle {
  text-transform: uppercase;
  position: fixed;
  top: 60px;
  right: 5px;
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl; 
  padding: 10px 5px 10px 2px;
  color: #fff;
  background-color: #76b976;
  border-radius: 5px 0 0 5px;
  z-index: 3;
  cursor: pointer;
  -webkit-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;  
  -webkit-box-shadow: -3px 3px 5px rgb(109, 109, 109);  
          box-shadow: -3px 3px 5px rgb(109, 109, 109);  
}

.stream_tab__toggle:hover {
  top: 57px;  
  -webkit-box-shadow: -5px 6px 10px rgb(109, 109, 109);  
          box-shadow: -5px 6px 10px rgb(109, 109, 109);  
}

.stream_tab__full_icon {
  position: absolute;
  cursor: pointer;
  right: 60px;
  top: 36px;
  opacity: 0.2;
}

.stream_tab__full_icon:hover {
  opacity: 1;
}

.stream_tab__close {
  position: absolute;
  cursor: pointer;
  right: 20px;
  top: 32px;
  width: 32px;
  height: 32px;
  opacity: 0.3;
}

.stream_tab__close:hover {
  opacity: 1;
}

.stream_tab__close:before, .stream_tab__close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 33px;
  width: 1px;
  background-color: #333;
}

.stream_tab__close:before {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.stream_tab__close:after {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}



